
export default {
  props: {
    items: {
      type: Object,
      required: true,
    }
  },
  methods: {
    onIntersect(event, observer, isIntersecting) {
      if (isIntersecting) {
        this.fetchNextPage();
      }
    },
    async fetchNextPage() {
      if (this.items.fetchNextPageImmediate !== undefined) {
        return await this.items.fetchNextPageImmediate();
      } else {
        return await this.items.fetchNextPage();
      }
    },
  }
}
